import React from 'react';
import { Panel, ListItem, Group, Div, Avatar, PanelHeader } from '@vkontakte/vkui';

const Balance = ({ id, go, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader>VK Play (βeta)</PanelHeader>
		{fetchedUser &&
		<Group title="Пользователь">
			<ListItem
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</ListItem>
        </Group>}
        
        
		<Group title="Баланс {наша валюта}">
			<Div>
				В разработке
			</Div>
		</Group>

        <Group title="Баланс VK Coin">
			<Div>
				В разработке
			</Div>
		</Group>
        </Panel>
);	

export default Balance;